import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TeamComponent } from './team.component';
import { WebfooterModule } from '@jgpl/shared/webfooter/webfooter.module';
import { WebheaderModule } from '@jgpl/shared/webheader/webheader.module';
import { MaterialModule } from '@jgpl/shared/material.module';
import { RouterModule } from '@angular/router';



@NgModule({
  declarations: [TeamComponent],
  imports: [
    CommonModule,
    MaterialModule,
    WebheaderModule,
    WebfooterModule,
    RouterModule.forChild([
      { path: '', component: TeamComponent }
    ])
  ]
})
export class TeamModule { }
